.eg-grid__field{--cellSize: min(2.2em, 6vw);--brColor: rgba(37, 38, 40, 0.5);--columns: 16;--rows: 7;border:1px solid var(--brColor);border-width:0 0 1px 1px;display:grid;grid-auto-flow:column;grid-template-columns:repeat(var(--columns), 1fr);grid-template-rows:repeat(var(--rows), 1fr)}.eg-grid__cursor{background-color:rgba(140,88,29,.25);grid-column:calc(var(--cursor) + 1);grid-row:1/span var(--rows);width:var(--cellSize);pointer-events:none}.eg-key{--face: #3c3f44;--button-gradient: radial-gradient(#8881, #0000);background-color:var(--face);background-image:var(--button-gradient);border:1px solid var(--brColor);border-radius:2px;border-width:1px 1px 0 0;box-sizing:border-box;display:grid;grid-column:calc(var(--step) + 1);grid-row:calc(var(--rows) - var(--note));grid-template-areas:"main";height:var(--cellSize);place-items:center;transition:background-color 100ms,color 100ms;-webkit-user-select:none;-ms-user-select:none;user-select:none;width:var(--cellSize)}.eg-key:before{content:"";grid-area:main;padding-top:100%}.eg-key>*{grid-area:main}.eg-key--is-active{--face: #d4902b;--gd-start: #fff566;--gd-end: #f000;--button-gradient: radial-gradient(var(--gd-start), var(--gd-end));box-shadow:0 0 16px 4px rgba(255,214,51,.1);color:#252628;transition:background-color 800ms,color 100ms;z-index:1}.eg-key__label{display:none;font-family:"Red Hat Mono";font-size:.625em;font-weight:100}
