.eg-logo{--gd-text-s0: #93989f;--gd-text-s1: #60656c;--gd-off-s0: #3c3f44;--gd-off-s1: rgba(60, 63, 68, 0.3);--gd-half-s0: rgba(212, 144, 43, 0.8);--gd-half-s1: rgba(212, 144, 43, 0.8);--gd-full-s0: #d4902b;--gd-full-s1: #ffd633;align-items:center;display:grid;grid-auto-flow:column;justify-content:start}.eg-logo path{transform-box:fill-box}.eg-logo #off-leds>path,.eg-logo #half-leds>path,.eg-logo #full-leds>path{transform:scale(0.7)}.eg-logo #full-leds>path{animation:flicker .0666666667s infinite}@keyframes flicker{0%{opacity:.9}50%{opacity:1}100%{opacity:.9}}.eg-logo__text{height:1em}.eg-logo__grid{height:1.4em}@media all and (max-width: 34em){.eg-logo{grid-gap:0;gap:0;grid-auto-flow:row}.eg-logo__text,.eg-logo__grid{height:auto;width:5em}}@media all and (max-width: 23em){.eg-logo{display:none}}
.eg-synthoptions{align-items:center;display:grid;grid-gap:2em;gap:2em;grid-template-columns:1fr auto auto auto;justify-self:stretch;padding:1em .5em 0}.eg-synth__option{align-items:center;display:grid;grid-gap:1em;gap:1em;grid-auto-flow:column;position:relative;-webkit-user-select:none;-ms-user-select:none;user-select:none;font-size:.85em}.eg-synth__led{display:none}.eg-synth__led+label{--size: 0.6em;grid-column-gap:.5em;-webkit-column-gap:.5em;column-gap:.5em;display:inline-grid;grid-template-areas:"led label";grid-template-columns:var(--size)}.eg-synth__led+label:before,.eg-synth__led+label:after{border-radius:var(--size);border:1px solid #3c3f44;content:"";grid-area:led;height:var(--size);place-self:center;width:var(--size)}.eg-synth__led+label:before{background-image:radial-gradient(#fff1 50%, #fbff0033)}.eg-synth__led+label:after{--size: 0.9em}.eg-synth__led:checked+label:after{background-image:radial-gradient(circle at 50% 40%, #fff566, #8c581d);box-shadow:0 0 .5em .1em #8c581d}.eg-synth__switch{display:none}.eg-synth__switch+label{--len: 1.7em;--flip-len: calc(var(--len) / 3);align-items:center;display:inline-grid;grid-gap:.5em;gap:.5em;grid-template-areas:"check label";grid-template-columns:var(--len) auto}.eg-synth__switch+label:before{align-items:center;background-color:#3c3f44;border-radius:1em;content:"";grid-area:check;height:1em;line-height:0;width:var(--len)}.eg-synth__switch+label:after{--color: #9198a1;background-color:var(--color);background-blend-mode:overlay;background-image:radial-gradient(circle at bottom, #fff9, #0008),radial-gradient(#0000 60%, #fff);border:1px solid #252628;border-radius:1em;content:"";grid-area:check;height:1em;justify-self:center;transform:translate(calc(-1 * var(--flip-len)), 0);transition:transform .08s ease-in-out;width:1em}.eg-synth__switch:checked+label:after{--color: #d4902b;transform:translate(var(--flip-len), 0)}
